.App {
  text-align: left
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Default layout for larger screens (computer browser) */
.faction-list {
  column-width: 250px; /* Fixed column width */
  column-gap: 20px; /* Space between columns */
  max-height: 90vh; /* Limit the height to avoid scrolling */
  overflow-y: auto; /* Allow scrolling if content exceeds the height */
  padding: 20px;
}

/* Each item in the faction list */
.faction-list > div {
  break-inside: avoid; /* Prevent breaking items across columns */
  margin-bottom: 20px; /* Space between items */
}

/* Stack items vertically on smaller screens */
@media (max-width: 768px) {
  .faction-list {
    column-width: auto; /* Disable multi-column layout */
    column-gap: 0; /* Remove column gap */
    display: flex;
    flex-direction: column;
  }

  .faction-list > div {
    margin-bottom: 10px; /* Adjust space between items for mobile */
  }
}

ul {
  list-style-type: none; /* Remove bullet points */
  padding-left: 0; /* Remove default left padding */
  margin-left: 0; /* Remove any additional margin */
}
/* Set global spacing between list items */
ul li, ol li {
  margin-bottom: 10px; /* Space between list items */
}

.faction-details {
  display: flex;
  gap: 20px; /* Space between columns */
  max-height: 100vh;
  overflow-y: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
}

/* Each column takes up 1/3 of the width on larger screens */
.column {
  flex: 1; /* 1/3 width of the container */
  display: flex;
  flex-direction: column;
}

.back-button {
  position: fixed; /* Fixed positioning at the top left */
  top: 0px;
  left: 0px;
  background-color: #ffffff; /* Button background color */
  color: black; /* Text color */
  padding: 2px 2px; /* Padding to make it look like a button */
  border: black solid 2px;
  border-radius: 5px; /* Rounded corners */
  text-decoration: none; /* Remove underline from the link */
  z-index: 1000; /* Ensure it appears above other content */
}

.back-button:hover {
  background-color: #dddddd; /* Darker color on hover */
}

/* Mobile view: stack all columns into one */
@media (max-width: 768px) {
  .faction-details {
    flex-direction: column; /* Stack columns vertically */
  }

  .column {
    width: 100%; /* Full width on mobile */
  }
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 200px;
  background-color: #333;
  color: white;
  padding: 20px;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  margin-bottom: 15px;
}

.sidebar a {
  color: white;
  text-decoration: none;
}

.sidebar a:hover {
  text-decoration: underline;
}

.app-container {
  display: flex;
}

.content {
  margin-left: 220px; /* Make space for the sidebar */
  padding: 20px;
  width: calc(100% - 220px);
}
